/* ===================== Í∏∞Î≥∏ Î†àÏù¥ÏïÑÏõÉ ===================== */

.sec5-section {
  position: relative;
  min-height: 100vh;
  background: #c64c2f; 
  color: #fff8f0;
  overflow: hidden;
}


.sec5-inner {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 96px 24px 120px;
}

.sec5-title {
  font-size: 50px;
  font-weight: 700;
  margin: 0 0 40px 0;
  text-align: left;
}



/* ===================== ÌïòÎã® Ïª®ÌÖêÏ∏† (QR + ÌÖçÏä§Ìä∏) ===================== */

.sec5-contact-wrap {
  max-width: 900px;
  display: flex;
  flex-direction: row;
  gap: 100px;
  align-items: start;
  margin: 0 auto;

}

.sec5-contact-card {
  max-width: 520px;
  font-family: "pretendard";
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 8px 0;
}

.sec5-copy {
  margin: 0;
  line-height: 1.8;
  color: #fff8f0;
  font-size: 18px;
  font-family: "pretendard";
}

.sec5-copy-strong {
  font-weight: 700;
}

/* Î≤ÑÌäºÎì§ */

.sec5-cta {
  margin-top: 50px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  font-family: "pretendard";
}

.sec5-btn {
  display: inline-block;
  padding: 10px 18px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  transition: transform 0.15s ease, box-shadow 0.15s ease, background 0.15s ease,
    color 0.15s ease, border-color 0.15s ease;
  font-family: "pretendard";
}

.sec5-btn-primary {
  background: #ffe3ae;
  color: #8a3c29;
  border: none;
}

.sec5-btn-primary:hover {
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.25);
  transform: translateY(-2px);
}

.sec5-btn-ghost {
  border: 2px solid #ffe3ae;
  color: #ffe3ae;
  background: transparent;
}

.sec5-btn-ghost:hover {
  background: rgba(255, 227, 174, 0.12);
}

/* ===================== QR Ïπ¥Îìú ===================== */

.sec5-qr-col {
  display: flex;
  justify-content: center;
}

.sec5-qr-box {
  background: #fff;
  color: #2f251f;
  border-radius: 24px;
  padding: 22px 18px 18px;
  text-align: center;
  box-shadow: 0 18px 40px rgba(38, 0, 0, 0.059);
  width: 300px; /* 320 ‚Üí 300ÏúºÎ°ú ÏÇ¥Ïßù Ï∂ïÏÜå */
  box-sizing: border-box;
}

.sec5-qr-box img {
  width: 100%;
  height: auto;
  border-radius: 18px;
}

.sec5-qr-caption {
  margin-top: 10px;
  font-weight: 400;
  font-family: "Pretendard";
  font-size: 13px;
}

.sec5-phone {
  margin-top: 4px;
  font-weight: 600;
  font-size: 18px;
  font-family: "Pretendard";
}

/* ===================== Î∞òÏùëÌòï ===================== */

@media (max-width: 900px) {
  .sec5-inner {
    padding: 72px 20px 96px;
  }

  .sec5-intro-line {
    text-align: center;
    margin-bottom: 36px;
  }

  .sec5-contact-wrap {
    grid-template-columns: 1fr;
    gap: 32px;
  }

  .sec5-qr-col {
    order: 1;
  }

  .sec5-contact-card {
    order: 2;
    text-align: center;
    align-items: center;
  }

  .sec5-cta {
    justify-content: center;
  }

  .sec5-qr-box {
    max-width: 300px;
    margin: 0 auto;
  }
}
