/* =========================
   Header.css
   ========================= */

.header {
  position: fixed;
  top: 8px;
  left: 0;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: right;
  align-items: center;
  z-index: 999;
  transition: background 0.3s ease, box-shadow 0.3s ease;
}

/* ÎÇ¥Î∂Ä Î©îÎâ¥ Î¶¨Ïä§Ìä∏ */
.header ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin-right: 2%;
  margin-top: 2%;

}

/* Í∞Å Î©îÎâ¥ Ìï≠Î™© */
.header li {
  padding: 8px 18px;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 500;
  font-size: 24px;
  position: relative;
  transition: color 0.2s ease;
}


/* ÏûëÏùÄ ÌôîÎ©¥ (Î™®Î∞îÏùº) ÎåÄÏùë */
@media (max-width: 768px) {
  .header {
    height: 60px;
  }

  .header ul {
    gap: 20px;
  }

  .header li {
    font-size: 14px;
  }
}

/* Ïä§ÌÅ¨Î°§ Ïãú Î∞∞Í≤Ω Í∞ïÏ°∞Ïö© ÌÅ¥ÎûòÏä§ (ÏÑ†ÌÉù ÏÇ¨Ìï≠)
   JSÏóêÏÑú Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏Î°ú .scrolled Ï∂îÍ∞Ä Í∞ÄÎä• */
.header.scrolled {
  background: rgba(255, 250, 243, 0.9);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
}