/* ============================= */
/* Section3 Í∏∞Î≥∏ Íµ¨Ï°∞            */
/* ============================= */
.sec3-section3 {
  position: relative;
  min-height: 100vh;
  background: #fff8f0; 
  color: #C64C2F;
  overflow: hidden;
}

.sec3-sec-inner {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 24px 50px;
}

.sec3-sec-title {
  font-size: 50px;
  font-weight: 700;
  margin: 0 0 40px 0;
}

/* ============================= */
/* Skill Ï†ÑÏ≤¥ Î†àÏù¥ÏïÑÏõÉ            */
/* ============================= */
.sec3-skill-box {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 150px;
}

/* ÏôºÏ™Ω Í∑∏Î¶¨Îìú */
.sec3-skills-grid {
  flex: 0 0 380px; /* ‚Üê ÏôºÏ™Ω Í≥†Ï†ïÌè≠ */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}


/* Ïò§Î•∏Ï™Ω */
.sec3-skill-note {
  display: grid;
  grid-template-rows: 300px 1fr; /* ‚Üê Í∞ÄÎ°ú ‚Üí ÏÑ∏Î°úÎ°ú Î≥ÄÍ≤Ω */
  justify-items: start;          /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  align-items: start;
  min-height: 520px;              /* Ï†ÑÏ≤¥ Ïπ¥Îìú ÎÜíÏù¥ ÏïΩÍ∞Ñ ÎäòÎ¶º */
  overflow: hidden;
  contain: layout paint;
  text-align: center;             /* Í∏ÄÏûê Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
}

/* ============================= */
/* Skill ÏïÑÏù¥ÌÖú                  */
/* ============================= */
.sec3-skill {
  background: rgba(255, 255, 255, .14);
  border: 1px solid rgba(255, 255, 255, .25);
  border-radius: 14px;
  padding: 12px 18px;
  text-align: center;
  transition: transform .2s ease, box-shadow .2s ease;
  will-change: transform;
}

.sec3-skill:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, .15);
}

.sec3-skill img {
  width: 60px;
  height: 60px;
  object-fit: contain;
  display: block;
  margin: 0 auto 8px;
}

.sec3-skill span {
  font-size: 14px;
}

/*-------- ÌÉúÍ∑∏ Ïπ©---------- */
.sec3-tags{
  display:flex; 
  flex-wrap:wrap; 
  gap:8px;
  margin:12px 0 ; 
  padding:0; 
  list-style:none;
}

.sec3-tags li{
  padding:6px 10px; 
  border-radius:999px;
  background:#f4e9e4; 
  color:#C64C2F; 
  font-size:13px;
  font-weight: 550;
  border:1px solid #ead7d2; 
  line-height:1;
  transition:transform .15s ease, box-shadow .15s ease;
}

.sec3-tags li:hover{ 
  transform:translateY(-1px); 
  box-shadow:0 2px 6px rgba(0,0,0,.06);
 }

/* Ï≤¥ÌÅ¨Î¶¨Ïä§Ìä∏ */
.sec3-bullets{ 
  margin:20px 0 0; 
  padding-left:5px; 
  list-style:none; 
}

.sec3-bullets li{
  position:relative; 
  padding-left:18px; 
  margin:6px 0; 
  color:#8f6b5f;
  font-size:14.5px;
  font-family: 'pretendard';
}

.sec3-bullets li::before{
  content:"‚úî";
  position:absolute;
  left:0;
  top:0;
  font-size:12px;
  color:#C64C2F; 
  line-height:1.6;
}

/* Î≤ÑÌäº */
.sec3-actions{ 
  display:flex; 
  gap:10px; 
  margin-top:14px;
 }

.sec3-actions .btn{
  display:inline-block; 
  padding:8px 14px; 
  border-radius:10px;
  background:#C64C2F;
   color:#fff; 
   font-size:14px; 
   border:1px solid #b6452a;
  box-shadow:0 4px 10px rgba(0,0,0,.08);
  transition:transform .12s ease, box-shadow .12s ease, background .2s ease;
}


.sec3-actions .btn:hover{ 
  transform:translateY(-1px); 
  box-shadow:0 6px 14px rgba(0,0,0,.12); 
}

.sec3-actions .btn.ghost{
  background:#fff8f0; 
  color:#C64C2F; 
  border:1px solid #e8cfc7;
}

.sec3-actions .btn.ghost:hover{ 
  background:#f7e8df; }


/* ÌîÑÎ¶¨Î∑∞ Ïπ¥Îìú */
.sec3-shot{
  display:block;
  width:100%; 
  max-width:500px;
  aspect-ratio:16/10;
  border:1px solid #ead7d2;
  border-radius:16px; 
  overflow:hidden;
  background:#fff; 
  box-shadow:0 8px 24px rgba(0,0,0,.06);
  margin:14px 0;
  cursor:zoom-in;
 transition:transform .15s ease;
}

.sec3-shot:hover{ 
  transform:translateY(-2px);
 }

.sec3-shot img{ 
  width:100%; 
  height:100%; 
  object-fit:cover; 
  display:block; 
}

/* Ïç∏ÎÑ§Ïùº Ïä§Ìä∏Î¶Ω */
.sec3-thumbs{ 
  display:flex; 
  gap:8px; 
  margin:6px 0 10px; 
  padding:0; 
  list-style:none; 
}

.sec3-thumbs li button{
  width:84px; 
  aspect-ratio:4/3; 
  border:1px solid #ead7d2; 
  border-radius:10px; 
  overflow:hidden; 
  background:#fff;
}

.sec3-thumbs img{ 
  width:100%; 
  height:100%; 
  object-fit:cover; 
  display:block; 
}


/* ÎùºÏù¥Ìä∏Î∞ïÏä§ */
.sec3-lightbox{
  position:fixed; 
  inset:0; 
  background:rgba(0,0,0,.6);
  display:grid; 
  place-items:center; 
  z-index:9999;
}


.sec3-lightbox-inner{
  position:relative; 
  max-width:min(92vw,1100px); 
  max-height:88vh;
  background:#111; 
  border-radius:14px; 
  padding:16px;
  box-shadow:0 20px 60px rgba(0,0,0,.4);
}


.sec3-lightbox-inner img{
  max-width:100%; 
  max-height:72vh; 
  display:block; 
  margin:0 auto; 
  object-fit:contain;
}


.sec3-lightbox-close{
  position:absolute; 
  top:8px; 
  right:10px; 
  font-size:28px; 
  line-height:1;
  width:38px; 
  height:38px; 
  border-radius:50%; 
  border:none; 
  background:#fff; 
  cursor:pointer;
}


.sec3-lightbox-nav{
  display:flex; 
  align-items:center; 
  justify-content:center; 
  gap:14px; 
  margin-top:10px; 
  color:#fff;
}

.sec3-lightbox-nav button{
  width:40px; 
  height:40px; 
  border-radius:999px; 
  border:none; 
  background:#fff; 
  cursor:pointer;
}



/* ============================= */
/* ÏõêÌòï Í∑∏ÎûòÌîÑ                   */
/* ============================= */
@property --val {
  syntax: "<number>";
  inherits: false;
  initial-value: 0;
}

.sec3-circle {
  --val: 0;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background: conic-gradient(#C64C2F calc(var(--val) * 1%), rgba(0, 0, 0, 0.06) 0);
  display: grid;
  place-items: center;
  position: relative;
  transition: --val .8s ease;
  flex-shrink: 0; /* ‚Üê Ï†àÎåÄ Ï§ÑÏñ¥Îì§ÏßÄ ÏïäÍ≤å */
}

.sec3-circle::after {
  content: attr(aria-label);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #fff8f0;
  display: grid;
  place-items: center;
  color: #C64C2F;
  font-weight: 400;
  font-size: 3.6rem;
  line-height: 1.3em;
  text-align: center;
}

@media (prefers-reduced-motion: reduce) {
  .sec3-circle { transition: none; }
}

/* ============================= */
/* Ïò§Î•∏Ï™Ω ÌÖçÏä§Ìä∏ ÏòÅÏó≠             */
/* ============================= */
.sec3-note-text {
  max-width: 500px; 
  /* ‚Üê Ìè≠ Í≥†Ï†ïÌï¥ÏÑú Í∏Ä Í∏∏Ïù¥Ïóê Îî∞Î•∏ ÎÑàÎπÑ Î≥ÄÌôî Î∞©ÏßÄ */
  text-align: left;
}

.sec3-note-text h3 {
  margin: 0 0 10px;
  font-size: 1.7rem;
  font-family: 'Pretendard';
  display: inline-block;
  width: auto;
  line-height: 2em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sec3-note-text p {
  font-family: 'Pretendard';
  margin: 0;
  color: #C64C2F;
  min-height: 48px; /* Î¨∏Ïû• Í∏∏Ïù¥ Îã¨ÎùºÎèÑ ÎÜíÏù¥ ÏùºÏ†ï */
}

/* ============================= */
/* ÌôúÏÑ±Ìôî                        */
/* ============================= */
.sec3-skill.active {
  background: #C64C2F;
  color: #fff;
  transform: translateY(-6px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
}

/* ============================= */
/* Î∞òÏùëÌòï                        */
/* ============================= */
@media (max-width: 1100px) {
  .sec3-skills-grid { grid-template-columns: repeat(4, 1fr); }
  .sec3-skill-note {
    grid-template-columns: 260px 1fr;
  }
  .sec3-circle { width: 260px; height: 260px; }
  .sec3-circle::after { 
    width: 210px; height: 210px;
  }
}

@media (max-width: 768px) {
  .sec3-skill-box { flex-direction: column; gap: 30px; }
  .sec3-skills-grid { flex: none; width: 100%; }
  .sec3-skill-note {
    grid-template-columns: 1fr;
    min-height: 320px;
    justify-content: center;
  }
  .sec3-note-text {
    max-width: 100%;
    text-align: left;
  }
  .sec3-note-text h3 {
    width: auto;
    text-align: left;
  }
}
