/* ================== sec4 Section ================== */
.sec4-section {
  position: relative;
  min-height: 100vh;
  background: #fff8f0;
  color: #2f251f;
  overflow: hidden;
}


/* Ïä¨ÎùºÏù¥Îçî Ï†ÑÏ≤¥ ÎûòÌçº */
.sec4-slider-wrap {
  position: relative;
  margin-top: 24px;
}

/* Í≥µÌÜµ ÌôîÏÇ¥Ìëú Ïä§ÌÉÄÏùº */
.sec4-arrow {
  position: absolute;
  top: 30%;
  z-index: 10;

  width: 44px;
  height: 44px;
  border-radius: 999px;
  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(6px);
  color: #fff;
  font-size: 26px;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

/* ÏôºÏ™Ω / Ïò§Î•∏Ï™Ω ÏúÑÏπò */
.sec4-prev {
  left: -100px;   /* ÌïÑÏöîÌïòÎ©¥ -10px, 20px Ïù¥Îü∞ ÏãùÏúºÎ°ú Ï°∞Ï†à */
}

.sec4-next {
  right: -100px;
}

/* Ìò∏Î≤Ñ Ïãú ÏÇ¥Ïßù Í∞ïÏ°∞ */
.sec4-arrow:hover {
  background: #c64c2f;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.25);
}

/* Î™®Î∞îÏùºÏóêÏÑúÎäî ÎÑàÎ¨¥ ÎãµÎãµÌïòÎ©¥ Ïà®Í∏∞Í±∞ÎÇò Ï§ÑÏù¥Í∏∞ */
@media (max-width: 768px) {
  .sec4-arrow {
    width: 32px;
    height: 32px;
    font-size: 20px;
  }

  .sec4-prev {
    left: -10px;
  }

  .sec4-next {
    right: -10px;
  }
}



.sec4-inner {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 70px 0 60px; 
}

.sec4-title {
  font-size: 50px;
  font-weight: 700;
  color: #c64c2f;
  margin: 0 0 40px 0;
  text-align: left;
}

/* swiper ÏÑ§Ï†ï */
.sec4-section .swiper {
  padding-bottom: 24px;
}
.sec4-section .swiper-slide {
  display: flex;
  justify-content: space-between;
}

/* ================== ÌîÑÎ°úÏ†ùÌä∏ Î†àÏù¥ÏïÑÏõÉ ================== */

.proj-media{
  position: relative;
  width: 100%;
  max-width: var(--media-max-w, 620px); /* ‚Üê Í∏∞Î≥∏ 720px, ÏóÜÏúºÎ©¥ 720 */
  margin: 0 auto;
}


.project{
  padding: 24px 0 0 24px;
  display: grid;
  /* 1Ïó¥ = ÎØ∏ÎîîÏñ¥ ÏµúÎåÄÌè≠ + Ï¢åÏ∏° Ïò§ÌîÑÏÖã(ÌîÑÎ°úÏ†ùÌä∏Î≥Ñ) */
   grid-template-columns: calc(min(var(--media-max-w, 620px), 38vw) + var(--media-col-offset, 0px)) 1fr;
  gap: var(--col-gap, 60px);  /* ÌîÑÎ°úÏ†ùÌä∏Î≥Ñ Í∞ÑÍ≤© */
  align-items: start;
}



/* Î™®ÎãàÌÑ∞ mock */
.monitor-mock {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  height: auto;
  pointer-events: none;
  
  filter: drop-shadow(0 6px 12px rgba(0, 0, 0, 0.16));
}

/* ================== 3-ÎîîÎ∞îÏù¥Ïä§ Î∞∞Ïπò ================== */
.proj-media--triple {
  position: relative;
  height: clamp(380px, 52vw, 560px);
}

/* Í≥µÌÜµ Ï†àÎåÄÎ∞∞Ïπò */
.proj-media--triple .device {
  position: absolute;

}

/* PC */
.proj-media--triple .device:nth-child(1) {
  width: 80%;
  left: 60%;
  top: 0;
  transform: translateX(-60%);
  z-index: 1;
}

/* Tablet */
.proj-media--triple .device:nth-child(2) {
  width: 38%;
  left: 4%;       
  bottom: 15%;
  z-index: 2;
}

/* Mobile */
.proj-media--triple .device:nth-child(3) {
  width: 20%;
  right: 0;
  bottom: 20%;
  z-index: 3;
}



/* ================== ÌôîÎ©¥Ï∞Ω ================== */
.monitor-screen-window {
  position: absolute;
  left: var(--screen-left);
  top: var(--screen-top);
  width: var(--screen-width);
  height: var(--screen-height);
  overflow: hidden;
  z-index: 1;
  cursor: ns-resize;
  clip-path: inset(0 round 12px);
}

.monitor-screen-img {
  display: block;
  width: 100%;
  height: auto;
  will-change: transform;
  user-select: none;
  pointer-events: none;
}

.sec4-section .swiper-slide:nth-child(3) .monitor-screen-window{
  border-radius: 27px;          /* clip-path Îë•Í∏ÄÍ∏∞ÏôÄ ÎßûÏ∂îÎ©¥ ÍπîÎÅî */
}
/* ================== ÌÖçÏä§Ìä∏ Ï†ïÎ≥¥ ================== */
.proj-info {
  font-family: 'Pretendard';
  max-width: 480px;
  display: flex;
  flex-direction: column;
  gap: 10px;  
}

.proj-title {
  margin: 0 0 20px;
  font-size: 24px;
  font-family: 'Pretendard';
}

.proj-meta {
  display: flex;
  gap: 8px;
  list-style: none;
  padding: 0;
  margin: 6px 0 12px;
  color: #7b6a62;
  font-size: 13px;   /* ÏÇ¥Ïßù ÏûëÍ≤å */
  margin-top: 12px;
}

.proj-meta li {
   margin: 0;
font-family: 'Pretendard';
}


.proj-skill-icons{
  display: flex;
  gap: 10px;
}

.proj-skill-icons img{
  width: 24px;
  height: 24px;
}


.proj-desc {
  line-height: 1.8;
  color: #4a3d36;
  font-family: 'Pretendard';
}



.proj-bullets-wrap {
  margin: 8px 0 10px;
}

.bullet-toggle {
  position: relative;                /* üî• Î∞òÎìúÏãú ÏûàÏñ¥Ïïº Ïñ∏ÎçîÎ∞îÍ∞Ä ÌùîÎì§Î¶¨ÏßÄ ÏïäÏùå */
  display: inline-flex;
  align-items: center;
  gap: 4px;

  padding: 0;
  margin: 0;

  background: transparent;
  border: none;

  color: #c64c2f;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  font-family: 'Pretendard';

  line-height: 1;                    
  height: 28px;                  

  transition: background .2s ease, transform .15s ease, box-shadow .2s ease;
}


.bullet-toggle::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0%;
  height: 1.5px;
  background: #c64c2f;
  transition: width .25s ease;
}

.bullet-toggle:hover {
  color: #c64c2f;   /* Î©îÏù∏Î≥¥Îã§ ÏÇ¥Ïßù ÏßÑÌïú ÌÜ§ */
  opacity: 0.9;
}

.bullet-toggle:hover::after {
  width: 100%;
}

.bullet-toggle-icon {
  padding-top: 5px;
   font-size: 24px;
}

/* ÏïÑÏΩîÎîîÏñ∏ ÎÇ¥Ïö© */
.proj-bullets {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: max-height 0.25s ease, opacity 0.25s ease, margin-top 0.25s;
  margin-top: 0;
}

.proj-bullets.is-open {
  max-height: 200px;  /* ÌïÑÏöîÌïòÎ©¥ ÎÜíÏù¥ Îçî ÌÇ§ÏõåÎèÑ Îê® */
  opacity: 1;
  margin-top: 6px;
}

.proj-bullets p {
  margin: 4px 0;
  line-height: 1.6;
  font-family: 'Pretendard';
  font-size: 14px;
}

.proj-actions {
  margin-top: 18px;
  display: flex;
  gap: 10px;
}

/* src/css/Section4.css ÌïòÎã®Ïóê Ï∂îÍ∞Ä */
.ps-actions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
  gap: 10px 12px;
  margin-top: 18px;

}


.ps-btn {
  justify-content: center;
  text-align: center;
  background: rgba(198, 76, 47, 0.07);
  border: 1px solid rgba(198, 76, 47, 0.25);
  color: #c64c2f;
  padding: 7px 14px;
  border-radius: 999px;
  font-weight: 500;
  font-size: 13px;
  font-weight: 600;
  line-height: 1;
   text-decoration: none;
  transition: transform .12s ease, box-shadow .12s ease, background .12s ease;
  font-family: 'Pretendard';
}



.ps-btn:hover {
  background: rgba(198, 76, 47, 0.18);
  border-color: rgba(198, 76, 47, 0.32);
}





/* Î∞òÏùëÌòï */
@media (max-width: 980px) {
  .ps-actions { gap: 8px; }
  .ps-btn { padding: 9px 12px; font-size: 14px; }
}



/* ================== Î∞òÏùëÌòï ================== */
@media (max-width: 1024px) {
  .project {
    grid-template-columns: 1fr;
    --media-col-offset: 0px;
  }
}

@media (max-width: 900px) {
  .project { grid-template-columns: 1fr; }
  .proj-media--triple { height: clamp(420px, 88vw, 620px); }
  .proj-media--triple .device:nth-child(1) { width: 84%; top: 4%; }
  .proj-media--triple .device:nth-child(2) { width: 54%; left: -2%; }
  .proj-media--triple .device:nth-child(3) { width: 28%; right: 0; }
}

@media (max-width: 700px) {
  .proj-media--triple { height: clamp(360px, 80vw, 520px); }
  .proj-media--triple .device:nth-child(1) { width: 78%; top: 6%; }
  .proj-media--triple .device:nth-child(2) { width: 46%; left: -2%; }
  .proj-media--triple .device:nth-child(3) { width: 26%; right: 0; }
}

