.section1 {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #C64C2F;
  color: #fff8f0;
  overflow: hidden;
}

.section1 .sec1-content {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 100px;
}

.section1 .sec1-content .sec1-text {
  flex: 1;
  text-align: center;
  font-size: 1.2rem;
}

.section1 .sec1-content .sec1-text h1 {
  font-size: 5.2rem;
  line-height: 1.2;
  margin: 0 0 50px 0;
  letter-spacing: -6px;
  font-weight: 100;
}

.sec1-text p {
  font-size: 1.7rem;
  font-family: 'Pretendard';
  margin: 0;
  line-height: 1.5;
}

.highlight-name {
  background-color: #fff8f0;
  color: #C64C2F; /* Î©îÏù∏ Ïª¨Îü¨ */
  padding: 1px 7px;
  font-weight: 500;
  display: inline-block;
  font-family: 'Pretendard';
}