/* reset for tags (Í∑∏ÎåÄÎ°ú Îë¨ÎèÑ Îê®) */
p, li, ul, span { margin: 0; padding: 0; }

/* ===== Section base ===== */
.sec2-section2 {
  position: relative;
  min-height: 100vh;
  color: #3b2a22;
  background: #fff8f0;
  overflow: hidden;
}

.sec2-sec-inner {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 24px 50px;
}

.sec2-sec-title {
  font-size: 50px;
  font-weight: 700;
  color: #c64c2f;
  margin: 0 0 40px 0;
}

/* ===== Layout ===== */
.sec2-profile-wrap {
  display: flex;
  gap: 15px;
}

.sec2-profile-left {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ===== Polaroid Group ===== */
.sec2-polaroid-group {
  position: relative;
  width: clamp(320px, 38vw, 520px);
  height: clamp(260px, 32vw, 420px);
}

.sec2-polaroid {
  position: absolute;
  background: #fff;
  padding: 12px 12px 38px;
  box-shadow: 0 10px 30px rgba(0,0,0,.08), 0 3px 8px rgba(0,0,0,.06);
}
.sec2-polaroid img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.sec2-caption {
  position: absolute;
  left: 14px;
  bottom: 10px;
  font-weight: 600;
  letter-spacing: 0.5px;
  color: var(--theme-accent, #C64C2F);
  font-family: 'pretendard';
}

.sec2-main {
  left: 0;
  top: 0;
 width: clamp(190px, 22vw, 300px);
  transform: rotate(-3.2deg);
  z-index: 1;
}

.sec2-sub {
  left: 48%;
  top:30%;
  width: clamp(130px, 15vw, 200px);
  transform: rotate(4.5deg);
  z-index: 2;
}

/* ===== Decorations ===== */
.sec2-dec-clip {
  position: absolute;
  left: 22px;
  top: -6px;
  width: clamp(22px, 3vw, 36px);
  z-index: 3;
  pointer-events: none;
  user-select: none;
}



/* ===== Right columns ===== */
.sec2-info-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}

.sec2-info-box {
  max-width: 760px;
  min-width: 300px;
  margin: 0 auto;
}

.sec2-info-title {
  font-size: 24px;
  font-weight: 500;
  margin: 5px 0 50px;
  color: #aa3e27;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5%;
  padding-left: 5px;
}

.sec2-ico {
  font-size: 30px;
}

/* License */
.sec2-license {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 50px;
  margin: 0;
  padding: 0; 
}

.sec2-license-list { 
  list-style: none; 
  padding: 0; 
  margin: 0; }
  
.sec2-license-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10%;
  padding: 12px 0;
  border-bottom: 1px dashed #f0d8cd;
  font-family: 'Pretendard';
}

.sec2-license-list li:first-child{
    padding: 0 0 12px 0;

}

.sec2-license-list li:last-child { border-bottom: 0; }
.sec2-license-list span { 
  font-weight: 600; 
  font-size: 14px;
  font-family: 'Pretendard';
 }
.sec2-license-list em { 
  font-style: normal; 
  color: #9b8a81; 
  font-size: 13px; 
  font-family: 'Pretendard';
 }

/* Note */

.sec2-profile-note {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.2em;
  font-weight: 600;
  color: #c64c2f;
  line-height: 1.35;
  margin-left: 30px;
}

.sec2-note-line:last-child{
  margin-left: 70px;

}
.sec2-note-line {
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

.sec2-type {
  display: inline-block;
  font-family: 'Pretendard';
  font-weight: 600;
  color: #c64c2f;
  background: linear-gradient(
    transparent 50%,   /* ÏúÑÏ™Ω Ï†àÎ∞òÏùÄ Ìà¨Î™Ö */
    #ffe3ae 50%,       /* ÎÖ∏ÎûÄÏÉâ ÏãúÏûë */
    #ffe3ae 90%,       /* ÎÖ∏ÎûÄÏÉâ ÎÅù */
    transparent 90%    /* ÏïÑÎûòÎäî Ìà¨Î™Ö */
  );
  padding: 0 2px;
  border-radius: 2px;
}

/* Ï≤´ Î≤àÏß∏ Ï§Ñ(Ïó¥Ï†ï...)Ïùò Ïª§ÏÑú Ïà®ÍπÄ */
.sec2-profile-note .sec2-note-line:first-child .sec2-caret {
  display: none;
}
.sec2-caret {
  display: inline-block;
  width: 2px;
  height: 1em;
  margin-left: 2px;
  background: #c64c2f;
  vertical-align: -2px;
  animation: sec2-caret-blink 1s steps(1) infinite;
}




@keyframes sec2-spin360 { 
  to { transform: rotate(360deg); } 
}

@keyframes sec2-pulseUp {
  0%, 100% { transform: translateY(0) scale(1); opacity: 1; }
  50% { transform: translateY(-2px) scale(1.05); opacity: .9; }
}
.sec2-spin  { 
  animation: sec2-spin360 1.2s linear infinite;
 }
.sec2-pulse { 
  animation: sec2-pulseUp 1.6s ease-in-out infinite;
}



/* ===== Education timeline (Pretendard + ÏßÄÍ∑∏Ïû¨Í∑∏ + ÍπîÎÅî Í∞ÑÍ≤©) ===== */
.sec2-edu-timeline {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: 'Pretendard', sans-serif; /* ‚úÖ Ìè∞Ìä∏ Í≥†Ï†ï */
    height: 100%;
}

.sec2-edu-timeline::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: calc(65%);
  background: #c64c2f;
  opacity: 0.9;
}
/* Í∞Å Ìï≠Î™© Í∏∞Î≥∏ (Í∞ÑÍ≤© 70px) */
.sec2-edu-timeline li {
  position: relative;
  width: 50%;
  height: 80px;
  box-sizing: border-box;
  font-family: 'Pretendard'; /* ‚úÖ Î™®Îì† Ìï≠Î™©Ïóê Ìè∞Ìä∏ Ï†ÅÏö© */
}

/* ÏôºÏ™Ω / Ïò§Î•∏Ï™Ω Î∞∞Ïπò */
.sec2-edu-timeline li:nth-child(odd) {
  left: 0;
  text-align: right;
  padding: 0 15px 0 0;
  
}
.sec2-edu-timeline li:nth-child(even) {
  left: 50%;
  text-align: left;
  padding: 0 0 0 15px;
}

/* Ï†ê */
.sec2-edu-timeline li::before {
  content: "";
  position: absolute;
  top: 0;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #c64c2f;
  z-index: 2;
}
.sec2-edu-timeline li:nth-child(odd)::before {
  right: -7px;
}
.sec2-edu-timeline li:nth-child(even)::before {
  left: -7px;
}


/* ÌÖçÏä§Ìä∏ Ïä§ÌÉÄÏùº (Pretendard Ïú†ÏßÄ) */
.sec2-edu-timeline strong {
  display: block;
  font-family: 'Pretendard'; /* ‚úÖ Î™ÖÏãúÏ†ÅÏúºÎ°ú Ìè∞Ìä∏ Í≥†Ï†ï */
  font-size: 14px;
  font-weight: 600;
  color: #5a372c;
  line-height: 1.55;
  letter-spacing: -1px;
  margin-bottom: 4px;
}
.sec2-edu-timeline em {
  display: block;
  font-family: 'Pretendard'; /* ‚úÖ Î™ÖÏãúÏ†ÅÏúºÎ°ú Ìè∞Ìä∏ Í≥†Ï†ï */
  font-size: 12.5px;
  color: #9b8a81;
  font-style: normal;
}

/* =========================
   Responsive media queries
   ========================= */


/* <= 1200px : Ïó¨Î∞±/Í∏ÄÍº¥ ÏÇ¥Ïßù Ï§ÑÏûÑ */
@media (max-width: 1200px) {
  .sec2-sec-inner { padding: 56px 20px 48px; }
  .sec2-sec-title { font-size: 44px; margin-bottom: 32px; }
  .sec2-info-columns { gap: 24px; }
  .sec2-info-title { margin: 0 0 36px; }

  .sec2-edu-timeline::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: calc(68%);
  background: #c64c2f;
  opacity: 0.9;
}
}


@media (max-width: 992px) {
  .sec2-profile-wrap {
    flex-direction: column;
    align-items: center;
    gap: 28px;
  }

  .sec2-polaroid-group {
    width: clamp(320px, 70vw, 520px);
    height: clamp(260px, 58vw, 420px);
  }
  .sec2-main { transform: rotate(-2.6deg); }
  .sec2-sub  { left: 54%; top: 28%; transform: rotate(4deg); }

  .sec2-info-columns { grid-template-columns: 1fr; }
  .sec2-info-box { max-width: 760px; width: 100%; }

  .sec2-profile-note { font-size: 18px; }
  .sec2-note-line:last-child { margin-left: 70px; }



  
}

/* <= 768px : Î™®Î∞îÏùº Í∏∞Ï§Ä Î†àÏù¥ÏïÑÏõÉ(Ìïú Ï§Ñ ÌÉÄÏûÑÎùºÏù∏) */
@media (max-width: 768px) {
  .sec2-sec-title { font-size: 36px; }

  .sec2-sub { left: 58%; top: 30%; width: clamp(120px, 40vw, 180px); }
  .sec2-s1  { left: 70%; top: 14%; width: 12%; }
  .sec2-s2  { right: 2%; bottom: 6%; width: 12%; }
  .sec2-s3  { left: -4%; bottom: -6%; width: 22%; }

  .sec2-license-list li { padding: 10px 0; }
  .sec2-license-list em { font-size: 12px; }

  .sec2-note-line:last-child { margin-left: 22px; }

  
}

/* <= 560px : ÏïÑÏ£º ÏûëÏùÄ ÌôîÎ©¥ ÏµúÏ†ÅÌôî */
@media (max-width: 560px) {
  .sec2-sec-inner { padding: 48px 16px 40px; }
  .sec2-sec-title { font-size: 32px; }

  .sec2-polaroid-group { width: 92vw; height: 66vw; }
  .sec2-main { width: clamp(180px, 58vw, 280px); }
  .sec2-sub  { left: 60%; top: 30%; width: clamp(110px, 34vw, 160px); }

  .sec2-s1, .sec2-s2 { opacity: .85; }
  .sec2-s3 { display: none; }

  .sec2-profile-note { align-items: center; }
  .sec2-note-line:last-child { margin-left: 0; }

  .sec2-license-list span { font-size: 14px; }
  .sec2-license-list em   { font-size: 12px; }

}


/* <= 400px : Í∑πÏÜåÌòï Îã®Îßê ÏïàÏ†ÑÎßù */
@media (max-width: 400px) {
  .sec2-sec-title { font-size: 28px; }
  .sec2-type { font-size: 16px; }
  .sec2-ico { width: 16px; height: 16px; margin-left: 12px; }

  .sec2-edu-timeline strong { font-size: 14px; }
  .sec2-edu-timeline em     { font-size: 12px; }
}
